* {
  font-size: 67.5%;
  box-sizing: border-box;
}

html {
  background: linear-gradient(to top left, rgb(59, 36, 80), rgb(92, 167, 157));
  height: 100vh;
}

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.bg-layer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-image: url("./bg/chains.jpg");
  background-repeat: repeat;
  background-size: contain;
  opacity: 0.015;
  z-index: -1000;
}

.App {
  text-align: center;
}

.loading-modal {
  position: fixed;
  z-index: 1500;
  background: #222;
  height: 100vh;
  width: 100%;
  opacity: 0.99;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.loader {
  height: 6rem;
  width: 6rem;
  animation: spin 3s infinite;
  opacity: 0.5;
}

.loading-msg {
  font-size: 3rem;
  color: white;
  font-family: "Lobster", cursive;
}

@keyframes spin {
  100% {
    transform: rotate(720deg);
  }
}

.title {
  font-size: 7rem;
  color: rgb(100, 1, 79);
  padding-top: 2rem;
  font-family: "Lobster", cursive;
  text-shadow: 2rem 2rem 1rem rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.image-box-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 3rem;
}

.image-box {
  height: 30rem;
  width: 30rem;
  border: 1px solid rgb(5, 75, 92);
  margin-left: 6rem;
  margin-right: 6rem;
  background: linear-gradient(to bottom right, rgb(3, 41, 33), rgb(42, 64, 71));
  border-radius: 2.2rem;
  box-shadow: 1rem 1rem 3.5rem rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  padding: 2.5rem;
  flex-direction: column;
}

.image-wrapper {
  box-sizing: content-box;
  height: 80%;
  width: 100%;
  background: white;
  border-radius: 0.5rem;
  overflow: hidden;
}

.image-name {
  color: white;
  font-size: 4rem;
  text-align: center;
  width: 100%;
  margin-top: 1rem;
}

.image {
  height: 100%;
  width: 100%;
}

.incorrect {
  border: 2px solid rgb(100, 9, 24);
}

.shake {
  animation: shake 0.15s infinite;
}

@keyframes shake {
  33% {
    transform: skew(-0.8deg);
  }
  66% {
    transform: skew(0.8deg);
  }
}

.answer {
  opacity: 0;
  position: fixed;
  border: 3px solid green;
}

.fade-in {
  animation: fadeIn 2s linear forwards;
}

.fade-out {
  animation: fadeOut 2s linear forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.attempt-wrapper {
  color: white;
  font-size: 6rem;
  padding-top: 2rem;
}

.next-btn {
  animation: fadeIn 3s linear forwards;
  height: 6rem;
  width: 15rem;
  border-radius: 1rem;
  outline: none;
  border: none;
  background: linear-gradient(
    to top left,
    rgb(63, 197, 108),
    rgb(105, 206, 164)
  );
  font-size: 2.9rem;
  font-weight: bold;
  box-shadow: 0.5rem 0.5rem 2rem rgba(0, 0, 0, 0.4);
  cursor: pointer;
  color: rgb(47, 20, 71);
  position: relative;
  top: -1.8rem;
}

.next-btn:hover {
  background: linear-gradient(to top left, rgb(54, 168, 92), rgb(89, 177, 140));
}

.input-section {
  position: fixed;
  bottom: 3rem;
  height: min-content;
  left: 50%;
  transform: translateX(-50%);
  padding: 2rem;
  width: 90rem;
}

.key {
  height: 5rem;
  min-width: 7rem;
  margin: 1rem 0.5rem 1rem 0.5rem;
  padding: 1.5rem 2rem;
  border-radius: 0.8rem;
  outline: none;
  cursor: pointer;
  background: linear-gradient(to top left, rgb(37, 37, 37), rgb(41, 41, 41));
  border: none;
  box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.4);
  color: rgb(233, 236, 182);
  font-size: 1.6rem;
  border: 1px solid rgb(2, 63, 73);
}

.key:hover {
  background: linear-gradient(to top left, rgb(43, 43, 43), rgb(46, 46, 46));
}

.key:active {
  color: white;
}

.del {
  color: rgb(238, 108, 91);
}

.enter {
  color: rgb(75, 199, 3);
}

.key:disabled {
  opacity: 0.2;
  cursor: none;
  pointer-events: none;
}

@media screen and (max-width: 1000px) {
  .image-box {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .image-name {
    font-size: 4.8vw;
  }

  .attempt-wrapper {
    padding-top: 0rem;
  }

  .key {
    height: 6vw;
    min-width: 8.2vw;
    padding: 2vw;
    font-size: 2.3vw;
  }
}

@media screen and (max-width: 800px) {
  .image-box-wrapper {
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .image-box {
    height: 43vw;
    width: 43vw;
    margin-left: 2vw;
    margin-right: 2vw;
  }
}

@media screen and (max-width: 750px) {
  .input-section {
    bottom: 4%;
  }

  .image-box {
    padding: 1.5rem;
  }

  .key {
    min-width: 6vw;
    height: 8vw;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
}

@media screen and (max-width: 600px) {
  .title {
    font-size: 5.5rem;
  }

  .next-btn {
    border-radius: 0.8rem;
  }

  .key {
    height: 9vw;
  }
}

@media screen and (max-width: 450px) {
  .title {
    padding-top: 0.5rem;
  }

  .attempt-wrapper {
    margin-top: -3rem;
  }

  .key {
    margin-left: 0.35rem;
    margin-right: 0.35rem;
    margin-top: 0.6rem;
    margin-bottom: 0.6rem;
    height: 12vw;
    font-size: 2.7vw;
  }
}

@media screen and (max-width: 400px) {
  .key {
    min-width: 7.5vw;
    height: 12vw;
  }
}

@media screen and (max-width: 350px) {
  .key {
    margin-left: 0.22rem;
    margin-right: 0.22rem;
    font-size: 2.8vw;
    height: 13vw;
  }
}
